
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default{
directives: {
TransferDom
},
data(){
return {
info: {},
id: this.$route.params.id,
list: [],
options: {
getThumbBoundsFn (index) {
var thumbnail = document.querySelectorAll('.previewer-demo-img')[index];
var pageYScroll = window.pageYOffset || document.documentElement.scrollTop;
var rect = thumbnail.getBoundingClientRect();
return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}
}
}
}
},
components: {
Previewer
},
created(){
var that = this;
ajax({
url: 'http://houxiaopang.com/api/v1.1/demanddetail',
type: "get",
data: {'demand_id': that.id},
success: function (data) {
if (data.code === 0) {
that.info = data.data;
for (var i = 0, size = that.info.desc_img.length; i < size; i++) {
that.list.push({'src': that.info.desc_img[i]});
}
} else {
alert("网络拥挤，请稍后再试···");
}
},
});
},
methods: {
show (index) {
this.$refs.previewer.show(index);
}
}
}



