
import XImg from 'vux/src/components/x-img/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default{
directives: {
TransferDom
},
components: {
XImg,
Previewer
},
data(){
return {
info: {},
demand_id: this.$route.params.id,
designer: {},
options: {
getThumbBoundsFn (index) {
var thumbnail = document.querySelectorAll('.previewer-demo-img')[index];
var pageYScroll = window.pageYOffset || document.documentElement.scrollTop;
var rect = thumbnail.getBoundingClientRect();
return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}
}
}
}
},
created(){
this.getdemandinfo(this.demand_id);
this.getpricelist(this.demand_id);
},
methods: {
setworklist(value){
var list = [];
value.forEach(function (item) {
list.push({'src': item});
});
return list;
},
getdemandinfo(id){
var that = this;
ajax({
url: 'http://houxiaopang.com/api/v1.1/demanddetail',
type: "get",
data: {'demand_id': id},
success: function (data) {
if (data.code === 0) {
that.info = data.data;
} else {
alert("网络拥挤，请稍后再试···");
}
},
});
},
setindex(){
console.log(this.imgindex);
var ind = this.imgindex + 1;
this.imgindex = ind;
return ind;
},
show (num, index) {
this.$refs['previewer' + index][0].show(num);
},
setimg(value){
value += "?imageView2/1/w/400/h/300";
return value;
},
getpricelist(id){
var that = this;
ajax({
type: 'get',
url: "http://houxiaopang.com/api/v1.1/adminsystem/pricelist_tmp",
data: {
'demand_id': id,
},
success(data){
if (data.code === 0) {
that.designer = data.data;
/* that.designer.forEach(function (item) {
item.worklist.forEach(function (imgs) {
that.imglist.push({'src': imgs});
});
});*/
} else {
alert("网络拥挤，请稍后再试···");
}
},
error(){
alert("网络拥挤，请稍后再试···");
}

});
}
}
}




